.slider {
  height: 500px;

  .slide {
    position: relative;
  }

  .linkContent {
    position: absolute;
    opacity: 0.6;
    left: 33%;
    top: 80%;

    a {
      text-decoration: none;
      background-color: transparent;
      border-radius: 5px;
      padding: 2px;
      transition: all 0.3s ease;
      color: white;
      border: 1px solid transparent;

      &:hover {
        background-color: #eae8e5;
        color: #000000;
      }
    }
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide img {
    display: block;
    width: 700px;
    height: 400px;
    object-fit: cover;
  }

}